$color-default: #159CEE;
$light-background: #F8FAFC;
$light-border: #E0E0EA;


* {
	padding: 0;
	font-family: 'Rubik', sans-serif;
	box-sizing: border-box;
	color: $color-default; }


*::placeholder {
	color: grey; }

.links-container {
	margin-top: 5px; }

.title {
	border-bottom: 1px solid $color-default;
	margin-bottom: 1rem;
	display: block;
	font-size: 1.5rem;
	padding-bottom: 30px;
	text-align: center;
	height: 4rem; }

.center {
	justify-content: center;
	align-items: center;
	display: flex; }

.main-title {
	font-size: 1.3rem;
	font-weight: 700;
	width: 180px;
	text-align: center;
	padding-top: 10px;
	align-self: flex-start;
	background-image: linear-gradient(rgba(21, 156, 238,.02), rgba(21, 156, 238,.3));
	border-radius: 10px;
	padding: 10px;
	margin-right: 40px; }





.search {
	position: fixed;
	display: flex;
	top: 0;
	left: 0;
	width: 100vw;
	height: 150px;
	box-shadow: 1px 1px 4px;
	align-content: center;
	align-items: center;
	justify-content: space-between;
	background-color: white; }

.container-flex {
	display: flex; }

.main {
	margin-top: 200px;
	padding-top: 30px;
	justify-content: space-around;
	width: 100%; }



.search-button {
	margin-left: 10px;
	padding: 5px;
	display: inline-block;
	border: 1px solid $light-border;
	border-radius: 3px;
	flex-shrink: 0;
	color: $color-default;
	width: auto;
	background-color: rgba(21, 156, 238,.04); }

.search-button:hover {
	background-color: white; }

a {
	text-decoration: none; }


a:visited, a:link {
	color: $color-default; }

.job-button {
	border: 1px solid $light-border;
	padding: 3px 8px;
	border-radius: 3px;
	background-color: white; }

.job-button:hover {
	background-color: rgba(21, 156, 238,.04); }



.jobs {
	width: 22.5vw; }

.loading {
	margin-top: 10px; }

.ellipsis * {
	text-overflow: ellipsis {
  white-space: nowrap;
  overflow: hidden; } }

.job {
	background-color: $light-background;
	padding: 3px;
	margin: 8px;
	border-radius: 5px;
	border: 1px solid $light-border; }




.search-button:hover, .search-button:focus {
	cursor: pointer; }


.search-button:active {

	color: grey; }

.search-input {
	margin-right: 10px;
	padding: 5px;
	font-size: 16px;
	flex-shrink: 0;
	border: 1px solid $light-border;
	outline: none; }




.search-container {
	margin-left: 20px; }



form {
	display: flex;
	align-items: center;
	justify-content: space-around; }


.buttons-container {
	justify-content: space-around; }


@media (max-width: 780px) {

	.search-container {
		flex-direction: column;
		height: 150px;
		align-content: center;
		justify-content: space-around; }

	.main-title {
		font-size: 1.2rem;
		order: 1;
		width: 100%; }

	.job {
		font-size: .6rem; }

	.jobs {
		padding-top: 40px;
		width: 24vw; }

	.title {
		font-size: 1.2rem; }

	.form {
		width: 100%;
		align-self: center;
		order: 2; }

	.search {
		height: 250px;
		justify-content: space-evenly;
		flex-direction: column;
		position: absolute; }


	.links-container {
		display: flex;
		flex-direction: column;
		align-items: center; }

	.job-button {
		width: auto;
		margin-top: 4px;
		width: 90%;
		text-align: center; }


	.search-input {
		margin: 0;
		width: 100%; }

	.search-button {
		margin: 5px;
		width: auto;
		min-width: 75px;
		flex-shrink: 0; } }




